@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.375em;
    height: 3.0625em;
    border-radius: 6.25em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-red-outline{
    border: 1.5px solid $primary;
    color: $black;
    &--white{
        color: $white;
    }
    &:hover{
        background-color: $primary;
        color: $white;
    }
}
