.header{
  padding-bottom: 3.125em;
  &__inner{
    padding-top: 4.5em;
    position: relative;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 13.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.menu{
  &__scroll{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav,
.footer-nav{
  ul{
    display: flex;
  }
  li{
    margin-right: 2.5em;
    &:last-child{
      margin: 0;
    }
  }
  a{
    font-size: 1.0625em;
    line-height: 118%;
  }
}

.footer-contacts,
.header-contacts{
  &__tg{
    img{
      width: 1.5em;
      margin-right: .75em;
    }
  }
}

.header-contacts{
  &__btn{
    margin-left: 1.875em;
  }
  &__email{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-bottom: 1.875em;
    position: relative;
    z-index: 20;
    &__inner{
      min-height: 4.0625em;
      display: flex;
      align-items: center;
      padding: 0;
    }
    &__logo{
      width: 7.8em;
      z-index: 11;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger__icon:before{
        transform: rotate(-45deg) translateY(.35em);
      }
      .burger__icon:after{
        transform: rotate(45deg) translateY(-.35em);
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 10em 0 3em;
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__scroll{
      padding: 0 1.25em;
      height: 100%;
      overflow-y: auto;
      display: block;
    }
  }

  .nav{
    ul{
      display: block;
    }
    li{
      margin: 0 0 1.5em;
    }
    a{
      font-size: 1.25em;
      line-height: 120%;
    }
  }

  .header-contacts{
    flex-direction: column;
    align-items: flex-start;
    margin-top: 4em;
    padding-top: 2em;
    border-top: 1px solid rgba(0,0,0,.2);
    &__btn{
      margin: 2em 0 0;
    }
    &__email{
      display: block;
      margin-top: .625em;
    }
    &__tg{
      img{
        order: 2;
        margin: 0 0 0 .75em;
      }
    }
  }

  .burger{
    display: flex;
    align-items: center;
    position: relative;
    z-index: 11;
    color: $black;
    &__text{
      font-size: .875em;
      text-transform: uppercase;
      letter-spacing: .08em;
    }
    &__icon{
      width: 1.25em;
      flex-shrink: 0;
      @include grid100gap(.375em);
      margin-right: .375em;
      &:before,
      &:after{
        content: '';
        width: 100%;
        height: 2px;
        background-color: $black;
        transition: all .3s;
      }
    }
  }
}


