.project-item{
  &__grid{
    display: grid;
    grid-template-columns: 57.5em;
    margin-top: 2em;
    position: relative;
  }
  &__sidebar{
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 57.5em - 1.875em);
    height: 100%;
  }
}

.gallery{
  overflow: hidden;
  .swiper-slide{
    padding-right: .375em;
  }
  .swiper{
    width: calc(100% + .375em);
  }
  &__small{
    margin-top: 1em;
    .swiper-slide{
      width: 10.375em;
      &-thumb-active{
        .gallery__img:before{
          opacity: 1 !important;
        }
      }
    }
    .gallery__img{
      cursor: pointer;
    }
    .gallery__img:before{
      content: '';
      position: absolute;
      border-radius: inherit;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      border: 2px solid $primary;
      z-index: 2;
    }
  }
  &__img{
    @include imgCover(67%);
    border-radius: .625em;
  }
  &__big{
    position: relative;
    .gallery__img{
      border-radius: 1.25em;
    }
  }
  .swiper-button{
    transform: translateY(-50%);
    margin: 0;
    &-prev{
      left: 1em;
    }
    &-next{
      right: 1em;
    }
    rect{
      fill: $black;
    }
  }
}

.project-info{
  position: sticky;
  top: 1em;
  padding: 2.5em 1.875em 2.75em;
  border-radius: 1.25em;
  background: #F5F5F5;
  &__top{
    @include grid100gap(.375em)
  }
  &__title{
    font-size: 1.5em;
  }
  &__group,
  &__props{
    padding-top: 2.25em;
    margin-top: 2.25em;
    border-top: 1px solid rgba(0, 0, 0, 0.20);
  }
  &__props{
    display: flex;
    justify-content: space-between;
  }
  &__prop{
    &-title{
      font-size: .875em;
      line-height: 120%;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: .05em;
    }
    &-bottom{
      margin-top: .25em;
    }
    &-num{
      font-size: 3.25em;
    }
    &-measure{
      font-size: 1.75em;
      letter-spacing: .05em;
    }
  }
  &__group{
    @include grid100gap(.75em);
    &-title{
      span{
        font-weight: 300;
        color: #444444;
      }
    }
    &-desc{
      font-size: 1em;
      line-height: 137%;
      font-weight: 300;
      color: #444444;
    }
  }
}


@media screen and (max-width: 992px){
  .project-item{
    overflow: hidden;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.75em;
    }
    &__sidebar{
      position: static;
      height: unset;
      margin-top: 1.875em;
      width: 100%;
    }
  }

  .gallery{
    overflow: unset;
    &__big{
      overflow: hidden;
    }
    &__small{
      margin-top: .625em;
      .swiper-slide{
        width: 6.5em;
      }
    }
    .swiper{
      overflow: unset;
      width: calc(100% + .25em);
    }
    .swiper-slide{
      padding-right: .25em;
    }
    .swiper-button{
      &-prev{
        left: .625em;
      }
      &-next{
        right: .625em;
      }
    }
  }

  .project-info{
    padding: 2.25em 1.25em 2.375em;
    border-radius: 1em;
    &__title{
      font-size: 1.25em;
    }
    &__group,
    &__props{
      padding-top: 1.75em;
      margin-top: 1.75em;
    }
    &__prop{
      &-title{
        font-size: .75em;
      }
      &-num{
        font-size: 2.625em;
      }
      &-measure{
        font-size: 1.375em;
      }
    }
    &__group{
      gap: .5em;
      &-title{
        font-size: .875em;
      }
      &-desc{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .gallery{
    &__big{
      .gallery__img{
        padding-bottom: 100%;
      }
    }
  }
}
