.projects-page{
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 5.5em 2.5em;
  }
}

.project-card2{
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(82.3%);
    border-radius: 1.25em;
  }
  &__badges{
    position: absolute;
    right: 1.25em;
    bottom: 1.75em;
    display: flex;
    flex-wrap: wrap;
    gap: .625em;
  }
  &__badge{
    white-space: nowrap;
    display: flex;
    align-items: center;
    padding: 0 1em;
    border-radius: 6.25em;
    background: $black;
    color: #DDD;
    height: 2.5625em;
    span{
      font-size: 1.125em;
    }
    &._red{
      background: $primary;
    }
  }
  &__bottom{
    margin-top: 2em;
    @include grid100gap(1em);
    max-width: 28em;
  }
  &__title{
    font-size: 1.75em;
    line-height: 120%;
  }
  &__address{
    color: #444;
  }
}

.projects-collection{
  &__title{
    letter-spacing: unset;
  }
}

.projects-slider{
  margin-top: 1.25em;
  .swiper-slide{
    width: 35.25em;
  }
}

.project-info2{
  padding: 2.5em 4em 3em 2em;
  border-radius: 1.25em;
  background: #F5F5F5;
  margin-top: 2.5em;
  @include grid100gap(2.25em);
  &__title{
    font-size: 1.75em;
    line-height: 120%;
  }
  &__desc{
    line-height: 150%;
    color: #444;
  }
}


@media screen and (max-width: 992px){
  .projects-page{
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: 3.125em;
    }
  }

  .project-card2{
    &__badges{
      right: .625em;
      bottom: 1.625em;
    }
    &__badge{
      height: 2.4375em;
      span{
        font-size: .9375em;
      }
    }
    &__bottom{
      margin-top: 1.25em;
      gap: .75em;
    }
    &__title{
      font-size: 1.25em;
    }
    &__address{
      color: #444;
    }
  }

  .projects-collection{
    overflow: hidden;
  }

  .projects-slider{
    margin-top: 1em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 30em;
    }
  }

  .project-info2{
    padding: 2.125em 1.25em 2.31em;
    border-radius: 1em;
    margin-top: 1.875em;
    gap: 1.75em;
    &__title{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .project-card2{
    &__img{
      padding-bottom: 100%;
    }
  }

  .projects-slider{
    .swiper-slide{
      width: 21.875em;
    }
  }
}
