.about{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 32.75em) 31.25em;
    gap: 2.5em 1.5em;
  }
  &__items{
    @include grid100gap(2em);
  }
}

.about-info{
  padding: 2.75em 3em 3.5em;
  border-radius: 1.25em;
  background: #F5F5F5;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  &:before{
    content: '';
    position: absolute;
    height: 5em;
    width: .5625em;
    background-color: $primary;
    left: 0;
    top: 3.5em;
  }
  &__years{
    color: $primary;
    margin-bottom: 1em;
    &-num{
      font-size: 6.25em;
    }
    &-title{
      font-size: 3.75em;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
  &__text{
    line-height: 150%;
  }
  &__nav{
    margin-top: 1.5em;
  }
  &__link{
    color: $primary;
  }
}

.about-item{
  min-height: 16.4375em;
  padding: 2em 2.5em;
  border-radius: 1.25em;
  border: 2px solid #DDD;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__top{
    padding-bottom: 1em;
  }
  &__num{
    font-size: 6.25em;
    display: inline;
    font-family: inherit !important;
  }
  &__measure{
    font-size: 2.5em;
    letter-spacing: .05em;
    margin-left: .25em;
  }
  &__bottom{
    width: 16.375em;
    margin-left: auto;
  }
}

.slider{
  .swiper-container{
    width: calc(100% + 1.5em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    padding-right: 1.5em;
    height: auto;
  }
}

.swiper-button{
  width: 3em;
  @include center;
  svg{
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .about-info{
    padding: 2.125em 1.125em 2.8125em;
    &:before{
      height: 3.375em;
      width: .4375em;
      top: 2.625em;
    }
    &__years{
      margin-bottom: 1em;
      &-num{
        font-size: 4.375em;
      }
      &-title{
        font-size: 2.5em;
      }
    }
    &__title{
      font-size: 1.75em;
    }
    &__text{
      font-size: 1em;
      line-height: 162%;
    }
  }

  .about-item{
    min-height: 11.25em;
    padding: 1.25em 1.125em;
    border-radius: 0.925em;
    border-width: 1.5px;
    &__num{
      font-size: 4.625em;
    }
    &__measure{
      font-size: 2em;
    }
    &__bottom{
      width: 12.375em;
    }
  }

  .slider{
    .swiper-container{
      width: calc(100% + .625em);
    }
    .swiper-slide{
      padding-right: .625em;
    }
  }

  .swiper-button{
    width: 2em;
  }
}


@media screen and (max-width: 576px){
    .about{
      &__items{
        grid-template-columns: 100%;
      }
    }
}
