.competence{
  padding: 3.125em 0 2.5em;
  &__inner{
    padding: 4em 3.69em;
    color: $white;
    border-radius: 1.25em;
    background: #333;
    display: grid;
    grid-template-columns: 36.5625em 34.5em;
    justify-content: space-between;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 3.25em;
  }
  &__text{
    @include grid100gap(2em);
  }
  &__title{
    font-size: 2.25em;
    line-height: 120%;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 150%;
  }
  &__items{
    @include grid100gap(3.25em);
  }
  &__item{
    display: grid;
    grid-template-columns: 4.5em calc(100% - 4.5em);
    &-num,
    &-text{
      font-size: 1.625em;
      line-height: 123%;
    }
    &-num{
      color: #979797;
    }
  }
  &__nav{
    display: flex;
    margin-top: 3.31em;
  }
  &__link{
    svg{
      width: 1.3125em;
      margin-left: 1em;
    }
    &:hover{
      svg{
        transform: translateX(.25em);
      }
      path{
        fill: $white;
      }
    }
  }
}

.arrow-link{
  display: flex;
  align-items: center;
  svg,
  img{
    width: 1.3125em;
    margin-left: 1em;
    transition: all .3s;
  }
  &._reverse{
    img{
      margin: 0 1em 0 0;
    }
    &:hover{
      img{
        transform: translateX(-.5em);
      }
    }
  }
  &:hover{
    svg,
    img{
      transform: translateX(.5em);
    }
  }
}

.m-section{
  padding: 3.125em 0;
  &._first{
    padding-top: 0;
  }
}

.projects{
  &__grid{
    margin-top: 1.75em;
  }
}

.project-card{
  position: relative;
  color: $white;
  display: block;
  &:hover{
    color: $white;
  }
  &__img{
    @include imgCover(0);
    height: 30.625em;
    border-radius: 1.25em;
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.00) 100%);
    }
  }
  &__top{
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 2.375em 1.75em;
    max-width: 20em;
  }
  &__title{
    font-size: 1.5em;
    line-height: 120%;
  }
  &__bottom{
    @include center;
    width: 2.5em;
    height: 2.5em;
    position: absolute;
    left: 1.25em;
    bottom: 1.25em;
    border-radius: 62.5em;
    background: $black;
    transition: all .3s;
    img{
      width: 1.125em;
    }
  }
  &:hover{
    .project-card__bottom{
      background-color: $primary;
      transform: translateX(.25em);
    }
  }
  &._big{
    @media screen and (min-width: 577px){
      grid-column: span 2;
    }
  }
}


@media screen and (max-width: 992px){
  .competence{
    padding: 1.625em 0 1.25em;
    &__inner{
      padding: 3.25em 1.25em 8.875em;
      grid-template-columns: 100%;
      gap: 3em;
      position: relative;
    }
    &__text{
      gap: 1.5em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1.125em;
      line-height: 133%;
    }
    &__items{
      gap: 1.75em;
    }
    &__item{
      grid-template-columns: 3.5em calc(100% - 3.5em);
      &-num,
      &-text{
        font-size: 1.25em;
        line-height: 140%;
      }
    }
    &__nav{
      position: absolute;
      left: 1.25em;
      bottom: 3.375em;
      margin: 0;
    }
  }

  .arrow-link{
    img{
      width: 1em;
    }
  }

  .m-section{
    padding: 1.5625em 0;
  }

  .projects{
    &__grid{
      margin-top: 1.125em;
    }
  }

  .project-card{
    &__top{
      padding: 2em 1.25em;
      max-width: 18.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .competence{
    &__item{
      grid-template-columns: 100%;
      gap: .6em;
    }
  }

  .projects{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    &__link{
      img{
        width: 1.3125em;
      }
      span{
        display: none;
      }
    }
  }
}
