@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 1.75em;
    li{
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '|';
                position: absolute;
                top: .125em;
                right: 100%;
                margin-right: .4375em;
            }
        }
        &:last-child{
            pointer-events: none;
        }
    }
    a,span{
        font-size: .875em;
        line-height: 143%;
        letter-spacing: .05em;
    }
    span{
        color: #888;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        margin-bottom: 1em;
        ul{
            font-size: .86em;
        }
    }
}
