@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: #1A1A1A;
    @include lastM(0 .75em 0 0);
    @include center;
    &:hover{
      path{
        fill-opacity: 1;
        &:not(._not-fill){
          fill: #fff;
        }
      }
    }
  }
}

.anim-newSing {
  opacity: 0;
  transition: opacity .3s;
  &--transform {
    transform: translateY(50px);
    transition: opacity .6s, transform 1s;
    transition-delay: .1s;
    //transform: translate(0%, 40px) matrix(1, 0, 0, 1, 0, 0);
    //will-change: opacity, transform;
    //&.in-view{
    //  animation: revealItem $duration+ms cubic-bezier(0.7, 0, 0.3, 1) forwards;
    //}
  }
  &._delay{
    transition-delay: .7s;
  }
  &.in-view {
    opacity: 1;
    &.anim-newSing--transform {
      transform: translate(0);
    }
  }
}


