.main-slider{
  color: $white;
}

.slider__wrap{
  overflow: hidden;
}

.main-slider{
  position: relative;
  .swiper-pagination{
    bottom: 2.69em;
  }
  .swiper-nav{
    display: flex;
    align-items: center;
    position: absolute;
    right: 3.75em;
    bottom: 4.25em;
  }
  .swiper-button{
    position: static;
    margin: 0 0 0 .5em;
  }
}

.swiper-pagination{
  @include center;
  .swiper-pagination-bullet{
    width: 0.75em;
    height: 0.75em;
    margin: 0 .4375em !important;
    opacity: 1;
    background-color: transparent;
    border: 2px solid $white;
  }
  &._rings{
    .swiper-pagination-bullet{
      position: relative;
      svg{
        transition: 0s;
        opacity: 0;
        @include centerAbsolute;
      }
      &-active{
        margin: 0 .75em !important;
        border-color: transparent;
        svg{
          opacity: 1;
        }
        .ring-progress{
          animation: fillRing 7s linear forwards !important;
        }
      }
    }
  }
}

@keyframes fillRing {
  from { stroke-dashoffset: 100; }
  to { stroke-dashoffset: 0; }
}

.progress-ring {
  width: 1.625em;
  height: 1.625em;
  transform:rotate(-90deg) translate(-50%,-50%);
}

.ring-bg { /*   */
  fill:none; stroke:#fff; stroke-width:3;
}

.ring-progress { /*   */
  fill:none;
  stroke: $primary;
  stroke-width:3;
  stroke-linecap:round;
  stroke-dasharray:100;
  stroke-dashoffset:100;
}

.main-card{
  height: 50em;
  position: relative;
  padding: 4em 3.69em;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  &__img{
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1.25em;
    @include imgCover(0);
    position: absolute;
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.00) 100%);
    }
  }
  &__top{
    pointer-events: none;
    position: absolute;
    top: 4em;
    left: 3.69em;
    z-index: 2;
    max-width: 50em;
    @include grid100gap(2em);
  }
  &__title{
    font-size: 3.75em;
    line-height: 133%;
    text-transform: uppercase;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
    max-width: 30em;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__subtitle{
    font-size: 2em;
    line-height: 119%;
  }
  &__link{
    margin-top: .875em;
    &:hover{
      color: $white;
    }
  }
}


@media screen and (max-width: 992px){
  .main-slider{
    .swiper-pagination{
      bottom: 1em;
    }
    .swiper-nav{
      display: none;
    }
  }

  .main-card{
    height: 30em;
    padding: 2em 1.25em 3.81em;
    &__top{
      max-width: 21em;
      gap: .5em;
      top: 2em;
      left: 1.25em;
      width: calc(100% - 2.5em);
    }
    &__title{
      font-size: 1.625em;
      line-height: 131%;
    }
    &__bottom{
      max-width: 19em;
    }
    &__subtitle{
      font-size: 1.25em;
    }
    &__link{
      margin-top: .5625em;
    }
  }
}


