.footer{
  padding: 2em 0 4em;
  &--border{
    .footer__top{
      padding-top: 3.75em;
      border-top: 4px solid #333;
    }
  }
  &__address{
    br{
      display: none;
    }
  }
  &__middle{
    align-items: flex-start;
    margin-top: 1.25em;
    &-col{
      padding-top: .5em;
    }
  }
  &__bottom{
    margin-top: 2.625em;
  }
}



.footer-contacts{
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  &__tg{
    margin-bottom: .81em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 1.25em;
    padding-bottom: 2.25em;
    &--border{
      .footer__top{
        padding-top: 2.5em;
      }
    }
    &__top{
      display: block;
      &-col2{
        margin-top: 2.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top,
    &__middle,
    &__bottom{
      padding-left: .625em;
      padding-right: .625em;
    }
    &__address{
      line-height: 143%;
      br{
        display: unset;
      }
    }
    &__middle{
      margin-top: .81em;
      flex-direction: column;
      &-col{
        padding-top: 3.25em;
        order: 2;
      }
    }
    &__bottom{
      margin-top: .875em;
      display: block;
    }
    &__c{
      margin-bottom: .875em;
    }
    &__conf{
      text-decoration: underline;
    }
  }

  .footer-nav{
    ul{
      display: block;
    }
    li{
      margin: 0 0 1.25em;
    }
  }

  .footer-contacts{
    align-items: flex-start;
    &__tg{
      img{
        order: 2;
        margin: 0 0 0 .75em;
      }
    }
  }
}
